<template>
  <v-flex  mt-8 mb-4 xs10
            md8
            offset-xs1
            offset-md2>
    <v-card
      class="mx-auto"
      style="display: inherit"
    >
      <v-card-title>Contact</v-card-title>
      <v-card-text>If you'd like to contact me you can do so either by submitting the contact form below or by e-mailing <b style="color:white; background: #1f1f1f; border-radius: 4px;">&nbsp;contact@ivandankov.com&nbsp;</b> directly.</v-card-text>
      
    </v-card>

    <v-card 
      class="mx-auto mt-4 mb-4"
    >
      <form class="pa-4" style="width: 100%">
        <v-text-field
          v-model="name"
          :error-messages="nameErrors"
          label="Name"
          @input="$v.name.$touch()"
          @blur="$v.name.$touch()"
        ></v-text-field>
        <v-text-field
          v-model="email"
          :error-messages="emailErrors"
          label="E-mail"
          required
          @input="$v.email.$touch()"
          @blur="$v.email.$touch()"
        ></v-text-field>
        <v-text-field
          v-model="subject"
          :error-messages="subjectErrors"
          label="Subject"
          required
          @input="$v.subject.$touch()"
          @blur="$v.subject.$touch()"
        ></v-text-field>
        <v-textarea class="mb-4"
          label="Message"
          required
        ></v-textarea>
        <!-- <v-select
          v-model="select"
          :items="items"
          :error-messages="selectErrors"
          label="Item"
          required
          @change="$v.select.$touch()"
          @blur="$v.select.$touch()"
        ></v-select>
        <v-checkbox
          v-model="checkbox"
          :error-messages="checkboxErrors"
          label="Do you agree?"
          required
          @change="$v.checkbox.$touch()"
          @blur="$v.checkbox.$touch()"
        ></v-checkbox> -->

        <v-btn class="mr-4" @click="submit">submit</v-btn>
        <v-btn @click="clear">clear</v-btn>
      </form>
    </v-card>
  </v-flex>
</template>

<script>
  import { validationMixin } from 'vuelidate'
  import { required, maxLength, email } from 'vuelidate/lib/validators'

  export default {
    mixins: [validationMixin],

    validations: {
      name: { },
      email: { required, email },
      select: { required },
      subject: { required },
      checkbox: {
        checked (val) {
          return val
        },
      },
    },

    data: () => ({
      name: '',
      email: '',
      subject: '',
      message: '',
      select: null,
      items: [
        'Item 1',
        'Item 2',
        'Item 3',
        'Item 4',
      ],
      checkbox: false,
    }),

    computed: {
      checkboxErrors () {
        const errors = []
        if (!this.$v.checkbox.$dirty) return errors
        !this.$v.checkbox.checked && errors.push('You must agree to continue!')
        return errors
      },
      selectErrors () {
        const errors = []
        if (!this.$v.select.$dirty) return errors
        !this.$v.select.required && errors.push('Item is required')
        return errors
      },
      nameErrors () {
        const errors = []
        if (!this.$v.name.$dirty) return errors
        return errors
      },
      emailErrors () {
        const errors = []
        if (!this.$v.email.$dirty) return errors
        !this.$v.email.email && errors.push('Must be valid e-mail')
        !this.$v.email.required && errors.push('E-mail is required')
        return errors
      },
      subjectErrors(){
        const errors = []
        if (!this.$v.subject.$dirty) return errors
        !this.$v.subject.subject && errors.push('Must be valid subject')
        !this.$v.subject.required && errors.push('Subject is required')
        return errors
      }
    },

    methods: {
      submit () {
        this.$v.$touch()
      },
      clear () {
        this.$v.$reset()
        this.name = ''
        this.email = ''
        this.select = null
        this.checkbox = false
        this.subject = ''
        this.message = ''
      },
    },
  }
</script>